@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;700&display=swap);
html {
  font-size: 10px;
  font-family: 'Nunito', sans-serif;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  background-color: rgb(244,244,244);
  color: #333;
}

p{
  font-size: 1.6rem;
}

small{
  font-size: 1.2rem;
}

.container{
  margin: 0 auto;
  max-width: 1000px;
  padding: 40px;
  text-align: center;
}

.title{
  font-size: 4.4rem;
}

.instructions{
  text-align: center;
  font-size: 20px;
}

.intro--image{
  width: auto;
  height: 350px;
  margin-bottom: 25px;
}


.form{
  display: flex;
  flex-direction: column;
}

.label{
  font-size: 1.2rem;
  margin-bottom: 0.2rem;
  text-transform: uppercase;
}

.input{
  width: 400px;
  font-size: 1.6rem;
  padding: 0.5rem 2rem;
  line-height: 2.8rem;
  border-radius: 20px;
  border: 1px solid #444444;
  margin-bottom: 1rem;
}

.input:focus{
  border: 1.5px solid #000000;
}

.button{
  height: 42px;
  width: 150px;
  margin-top: 2rem;
  background-color: #ff4b33;
  color: white;
  padding: 1rem 2rem;
  border: 1px solid rgba(0, 0, 0, 0.75);
  border-radius: 20px;
  font-size: 2rem;
  cursor: pointer;
  transition: background-color 250ms;
}

.button:hover{
  background-color: #f03017;
}

@media (min-width: 786px){
  .form{
    grid-template-columns: auto 1fr auto;
    grid-gap: 1rem;
    align-items: center;
  }

  .input{
    margin-bottom: 0;
  }
}

.card--list{
  margin-top: 4rem;
}

.card{
  padding: 2rem 4rem;
  border-radius: 10px;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.25);
  margin-bottom: 2rem;
  margin-top: 2rem;
  background-color: white;
}

.card--title{
  margin-bottom: 0.5rem;
  font-size: 3.2rem;
}

.card--image{
  margin: 0 auto;
  display: block;
  border-radius: 10px;
}


